<template>
  <div @click.stop="toggle"
       v-if="is_del">
    <div class="top"
         :class="{'resetOne': level > 0}">
      <div class="list">
        <div class="title"
             :style="{'paddingLeft': paddingLeft}">
          <!-- :style="{ visibility: !obj.sub || obj.sub.length <= 0 ? 'visible' : 'hidden'}" -->
          <v-icon class="icon"
                  :class="{'transformIcon': showChildren}"
                  :style="{ visibility: obj.children&&obj.children.length > 0 ? 'visible' : 'hidden'}">mdi-menu-down</v-icon>
          <div class="title_name">{{INT(obj.org_level_name)}}</div>
        </div>
        <div class="staffNum">
          {{obj.staff_number}}
        </div>
        <div class="action">
          <menu-list :list='list'
                     @handleClick='handleClick'>
            <template v-slot:showAction>
              <v-icon>mdi-dots-vertical</v-icon>
            </template>
          </menu-list>

        </div>
      </div>
      <div v-if="obj.children.length>0&&showChildren">
        <department-list v-for="(item,index) in obj.children"
                         :key='index'
                         :obj='item'
                         @delData='delData'
                         :level='level + 1'></department-list>
      </div>

    </div>
  </div>
</template>

<script>
import { delDepartment } from '@/service/api/departmentManage'
export default {
  name: 'departmentList',
  components: {},
  props: {
    level: {
      type: Number,
      default: 0
    },
    obj: {
      type: Object
    }
  },
  data () {
    return {
      is_del: true,
      showChildren: false,
    };
  },
  created () { },
  mounted () { },
  methods: {
    toggle () {
      if (this.obj.children.length <= 0) {
        return
      }
      this.showChildren = !this.showChildren
    },
    delData (uuid) {
      this.$emit('delData', uuid)
    },
    handleClick ({ item, index }) {
      if (item.type == 'view') {
        this.$store.commit('set_subPage_name', this.obj.org_level_name)
        this.$store.commit('set_uuid2', this.obj.uuid)

        this.$store.commit('set_iconstatus', true)
        this.$router.push({
          name: 'department_detail',
          params: {
            name: this.obj.org_level_name,
            uuid: this.obj.uuid
          }
        })
      }
      if (item.type == 'edit') {
        this.$store.commit('set_subPage_name', this.obj.org_level_name)
        this.$store.commit('set_uuid2', this.obj.uuid)

        this.$store.commit('set_iconstatus', false)
        this.$router.push({
          name: 'department_detail',
          params: {
            name: this.obj.org_level_name,
            uuid: this.obj.uuid
          }
        })
      }

    }
  },
  computed: {
    paddingLeft () {
      return this.level * 15 + 'px'
    },
    list () {
      return [
        {
          name: this.$t('label_View'),
          type: 'view'
        },
        {
          name: this.$t('label_Edit'),
          type: 'edit'
        }
      ]
    }
  },
  watch: {},
};
</script>

<style scoped>
.top {
  width: 100%;
  border: 1px solid #dedede;
  border-radius: 8px;
  margin: 10px 0;
}
.list {
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.list:hover {
  background-color: #ececec;
}
.title {
  display: flex;
  align-items: center;
  flex: 1;
  /* width: 23vw; */
}
.staffNum {
  width: 5vw;
  margin-right: 10vw;
}
.action {
  width: 2vw;
  text-align: right;
  cursor: pointer;
}
.title_name {
  font-size: 16px;
}
.icon {
  transform: rotate(-90deg);
}

.transformIcon {
  transform: rotate(0deg);
}

.d-flex {
  align-items: center;
  font-size: 17px;
}
.resetOne {
  border: none;
  border-top: 1px solid #dedede;
  /* border-bottom: 1px solid #dedede; */
  border-radius: 0px;
  margin: 0;
}
</style>
